/* Scatterplot.css */
.d3-component {
    position: absolute;
    top:0%;
    left: 0%;
    width: 82%;
    height: 100%;
    background-color: #fff;
  }

.chart-component {
    position: absolute;
    top: 86%;
    left: 15%;
    width: 100%;
    height: 30%;
  }

  .iconbar-component {
    position: absolute;
    top:0%;
    left: 82%;
    width: 12%;
    height: 100%;
    background-color: #fff;
  }

.icon-hover:hover {
    opacity: 1;
}

  
  
 
  
  
  /* Add this to your main CSS file */
  .app-container {
    font-family: 'Arial', sans-serif;
    color: #333;
    margin: 0;
    padding: 0;
    
  }
  
  header {
    background-color: #072340;
    color: #fff;
    padding: 10px 10px;
    text-align: center;
  
  }
  
  main {
    padding: 20px;
  }
  
  footer {
    background-color: #f8f9fa;
    padding: 10px 0;
    position: fixed;
    bottom: 0;
    width: 100%;
    text-align: center;
  }
  
  
  .ellipses-field {
    position: absolute;
    top: 80%;
    left: 82%;
    width: 12%;
    height: 2%;
    margin-left: 5px;
    padding: 10px 15px;
    background-color: #ffffff;
    color: rgb(0, 0, 0);
    z-index: 10;
    border: solid gray 1px;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  

  .iter-info {
    position: absolute;
    top: 80%;
    left: 30%;
    width: 20%;
    height: 5%;
    background-color: #ffffff;
    padding: 2px 2px 2px 2px;
    color: rgb(0, 0, 0);
    z-index: 10;
    border: solid gray 1px;
    border-radius: 5px;
  }

.method {
    position: absolute;
    top: -8%;
    left: 2%;
    width: 20%;
    height: 5%;
    padding: 10px 10px 10px 10px;
    color: rgb(0, 0, 0);
    z-index: 10;
    border: none;
    border-radius: 5px;
  }

  
  
  